import { makeStyles } from '@mui/styles';

import { Typography, styled } from '@mui/material';
import { isMobile } from '../styles/isMobile';
import j1 from '../assets/fondoTarjeta.gif';
import sizeLetter from '../styles/fontSize';
import { AppContext } from '../context/appContext';
import { useContext } from 'react';
import colores from '../styles/colors';

const GameCard = ({ cardData, i }) => {
	const { setRuta } = useContext(AppContext);
	const classes = useStyles();

	return (
		<StyledCard onClick={() => setRuta(cardData.link)}>
			<div id='imageDiv' className={classes.imageDiv}>
				<div id='hoverTxt' className={classes.hoverTxt}>
					<Typography className={classes.title}>
						{cardData.title.toUpperCase()}
					</Typography>
				</div>
			</div>
		</StyledCard>
	);
};

const StyledCard = styled('div')({
	cursor: 'pointer',
	border: '1px solid #fff',
	width: isMobile() ? '80%' : '38vh',
	height: isMobile() ? '14vh' : '56vh',
	margin: 'auto',
	marginBottom: isMobile() && 30,
	filter: 'drop-shadow(1px 1px 3px #fff)',
	'@keyframes zoom': {
		from: {
			opacity: 0,
			transform: `scale(0) ${!isMobile() && 'rotate(140deg)'}`,
		},
		to: {
			opacity: 1,
			transform: 'scale(1) ',
		},
	},
	animation: isMobile() ? 'zoom 1s' : 'zoom .5s',
});

const useStyles = makeStyles((theme) => ({
	imageDiv: {
		height: '100%',
		width: '100%',
		position: 'relative',
		background: `url(${j1})`,
		//: "#f2f2f2",
		backgroundSize: 'cover',
		backgroundPosition: 'center center',
		display: 'table',
		transition: '.3s',
		transitionTimingFunction: 'ease',
	},
	hoverTxt: {
		height: '100%',
		display: 'flex',
		flexDirection: 'column',
		alignItems: 'center',
		justifyContent: 'center',

		color: colores.mainStrong,
		transition: '.3s',
	},
	title: {
		paddingLeft: '4%',
		paddingRight: '4%',
		fontFamily: 'Poppins',
		fontWeight: 800,
		fontSize: isMobile()
			? [sizeLetter.cellBig, '!important']
			: [sizeLetter.big, '!important'],
	},
	txt: {
		fontFamily: 'Poppins',
		fontSize: [sizeLetter.small, '!important'],
		transform: 'translateY(2000%)',
		transition: '.2s',
		transitionDelay: '.1s',
		transitionTimingFunction: 'ease',
	},
}));

export default GameCard;
