import { CssBaseline } from '@mui/material';
import { createTheme, ThemeProvider } from '@mui/material/styles';
import './App.css';
import BodyLayout from './layout/body';
import AppProvider from './context/appContext';
import { Suspense, useEffect, useState } from 'react';
import RutasApp from './rutasApp';
import Loader from './components/loader';

import backgroundAnim from './assets/fondoAnimado.gif';
import fondoTarj from './assets/fondoTarjeta.gif';
import brain from './assets/cerebro-04.svg';
import { cacheImages } from './helper/carcheImages';
const w = window.innerWidth;
const theme = createTheme({
	typography: {
		fontFamily: 'Poppins, Arial',
	},
});

function App(props) {
	const [loader, setLoader] = useState(true);
	console.log('@ ', w);
	useEffect(() => {
		const img = [backgroundAnim, fondoTarj, brain];

		const result = cacheImages(img);
		console.log('aca vaaaaa');
		if (result) setLoader(false);
	}, []);

	return (
		<AppProvider>
			<ThemeProvider theme={theme}>
				<CssBaseline />
				<Suspense fallback={<Loader />}>
					{!loader ? (
						<BodyLayout>
							<RutasApp />
						</BodyLayout>
					) : (
						<Loader />
					)}
				</Suspense>
			</ThemeProvider>
		</AppProvider>
	);
}

export default App;
