import { Grid, Button, Fade, Card, Typography } from '@mui/material';
import { makeStyles } from '@mui/styles';
import { useContext } from 'react';
import { AppContext } from '../../context/appContext';
import cerebroPng from '../../assets/cerebro-04.svg';
import colores from '../../styles/colors';
import SpringTxt from '../../components/springTxt';
import sizeLetter from '../../styles/fontSize';
import FinalCard from '../../components/finalCard';
import { isMobile } from '../../styles/isMobile';
import '../../styles/shakeeff.css';
import '../../styles/brain.css';
import CardBrain from './cardBrain';
import billete from '../../assets/iconCard/billete.svg';
import carritoSuper from '../../assets/iconCard/carritoSuper.svg';
import mamadera from '../../assets/iconCard/mamadera.svg';
import plato from '../../assets/iconCard/plato.svg';
import reloj from '../../assets/iconCard/reloj.svg';
import ropa from '../../assets/iconCard/ropa.svg';

const Game2 = () => {
	const { dataJuegoDos, activeStep } = useContext(AppContext);
	const classes = useStyles();

	return (
		<Grid
			container
			spacing={!isMobile() && 8}
			/*   style={{
        minHeight: !isMobile() && "80vh",
      }} */
		>
			<Grid item md={5} xs={12} className={classes.centerContent}>
				<CardBrain />
			</Grid>
			<Grid
				item
				md={6}
				xs={12}
				className={classes.centerContent}
				style={{
					marginTop: isMobile() ? 110 : '6%',
				}}
			>
				{dataJuegoDos[activeStep] === undefined ? (
					<FinalCard />
				) : (
					<CardResp />
				)}
			</Grid>
		</Grid>
	);
};

const CardResp = () => {
	const {
		dataJuegoDos,
		score,
		handlerStep,
		activeStep,
		showEff,
		setShowEff,
	} = useContext(AppContext);
	const classes = useStyles({ score, dataJuegoDos, activeStep });

	function handleSituation(btn) {
		setShowEff();
		setTimeout(() => {
			handlerStep(dataJuegoDos[activeStep].respuesta, btn);
		}, 900);
	}

	return (
		<Card className={classes.cardResp}>
			{<BorderIcons />}
			<div className={classes.SpringTxtRep}>
				<SpringTxt
					open={showEff}
					txts={dataJuegoDos[activeStep].pregunta}
					lspacing={0}
					whiteSpace={true}
					color={colores.mainStrong}
					//color="#383838"
					size={isMobile() ? sizeLetter.cellExtraBig : sizeLetter.Big}
				/>
			</div>
			<div style={{ display: 'flex', flexDirection: 'row' }}>
				<Button
					disabled={!showEff}
					color='secondary'
					variant='contained'
					className={classes.btn}
					onClick={(e) => handleSituation('true')}
				>
					<Typography className={classes.btnText}>SI</Typography>
				</Button>
				<Button
					disabled={!showEff}
					variant='contained'
					className={classes.btn}
					onClick={(e) => handleSituation('false')}
				>
					<Typography className={classes.btnText}>NO</Typography>
				</Button>
			</div>
		</Card>
	);
};

const BorderIcons = () => {
	return (
		<div
			style={{
				position: 'absolute',

				height: 50,
				width: '100%',
				height: '100%',

				//margin: isMobile() ? -30 : -50,
			}}
		>
			{iconCardData.map((conpData) => (
				<div
					id={conpData.id}
					style={{
						hidden: 'true',
						position: 'absolute',
						height: isMobile() ? '9vw' : '5vw',
						width: isMobile() ? '9vw' : '5vw',
						filter: `drop-shadow(1px 1px 1px #000)`,
						left: conpData.left,
						right: conpData.right,
						top: conpData.top,
						bottom: conpData.bottom,
						margin: isMobile() ? -4 : -25,
					}}
				>
					<img src={conpData.icon} height='100%' width='100%' />
				</div>
			))}
		</div>
	);
};

const iconCardData = [
	{
		icon: plato,
		id: 'topLeft',
		right: null,
		left: 0,
		top: 0,
		bottom: null,
	},
	{
		icon: ropa,
		id: 'topRight',
		right: 0,
		left: null,
		top: 0,
		bottom: null,
	},

	{
		icon: billete,
		id: 'downLeft',
		right: null,
		left: 0,
		top: null,
		bottom: 0,
	},
	{
		icon: carritoSuper,
		id: 'downRight',
		right: 0,
		left: null,
		top: null,
		bottom: 0,
	},

	{
		icon: mamadera,
		id: 'centerLeft',
		right: -20,
		left: null,
		top: '50%',
		down: null,
	},
	{
		icon: reloj,
		id: 'centerRight',
		left: 0,
		right: '50%',
		top: '50%',
		down: null,
	},
];

const useStyles = makeStyles(() => ({
	brainContainer: {
		position: 'relative',
		height: isMobile() ? '30vh' : '100%',

		marginLeft: isMobile() && -30,
		width: '100%',
		transition: 'all 0.5s ease',
	},

	centerContent: {
		display: 'flex',
		flexDirection: 'column',
		alignItems: 'center',
		justifyContent: 'center',
	},

	cardResp: {
		overflow: 'visible',
		background: 'rgba(255,255,255,.9)!important',
		paddingBottom: !isMobile() ? 30 : 15,
		width: isMobile() ? '100%' : '106%',
		height: !isMobile() ? '64vh' : '34vh',
		borderRadius: [15, '!important'],
		display: 'flex',
		textAlign: 'center',
		flexDirection: 'column',
		alignItems: 'center',
		justifyContent: 'space-between',
		filter: `drop-shadow(3px 3px 3px ${colores.mainStrongTr})`,
		borderBottom: `8px solid ${colores.mainStrong}`,
	},
	SpringTxtRep: {
		height: !isMobile() ? '48vh' : '30vh',

		display: 'flex',
		textAlign: 'center',
		flexDirection: 'column',
		alignItems: 'center',
		justifyContent: 'space-between',
	},
	btn: {
		width: isMobile() ? '10vh' : '8vw',
		height: isMobile() ? '4vh' : '4vw',

		backgroundColor: [colores.mainStrong, '!important'],

		marginRight: isMobile() ? [15, '!important'] : [35, '!important'],
		marginLeft: isMobile()
			? '4vh'[(15, '!important')]
			: [35, '!important'],
		transition: ['transform .4s', '!important'],
		'&:hover': {
			transform: 'scale(1.1)',
		},
	},
	btnText: {
		fontFamily: 'Poppins',
		fontWeight: 'bold!important',
		fontSize: !isMobile()
			? [sizeLetter.big, '!important']
			: [sizeLetter.cellBig, '!important'],
	},
}));

export default Game2;
