import fondo from '../assets/fondoAnimado.gif';
import fondoSinLogo from '../assets/fondoSinLogo.gif';
import '../styles/backgroundFall.css';
import { useContext } from 'react';
import { AppContext } from './../context/appContext';
const windowWidth = window.screen.width;

const BodyLayout = (props) => {
	const { ruta } = useContext(AppContext);
	return (
		<div
			style={{
				backgroundImage:
					ruta !== 'home' ? `url(${fondoSinLogo})` : `url(${fondo})`,
				//backgroundColor: "purple",
				backgroundSize: 'cover',
				backgroundPosition: 'center center',
				paddingRight: windowWidth < 600 ? '4%' : '10%',
				paddingLeft: windowWidth < 600 ? '4%' : '10%',
				minHeight: '100vh',
				overflow: 'hidden',
				display: 'flex',
				flexDirection: 'column',
				alignItems: 'center',
				justifyContent: 'center',
				width: '100%',
			}}
		>
			{props.children}
		</div>
	);
};

export default BodyLayout;
